body {
  text-align: center;
}

ul {
  padding: 0;
}

.conteiner {
  display: flexbox;
  margin: 0 auto;
  padding: 10px 20px;

  max-width: 1200px;
  width: 1200px;
}

form {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
}

.photo-card {
  margin: 25px 0px;
  width: 300px;
  height: 400px;
  min-width: 300px;
}

.stats {
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

.stats-item {
  align-items: center;
}

.image {
  min-height: 300px;
  width: 100%;
}
